




























































	import MescrollUni from "../../components/mescroll-diy/mescroll-meituan.vue";
	import common from '../../common/common.js';
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	import empty from "@/components/empty";
	import {
		mapState
	} from 'vuex';
	export default {
		components: {
			MescrollUni
		},
		data() {
			return {
				mescroll: null, //mescroll实例对象
				// 下拉刷新的配置
				downOption: {
					use: true, // 是否启用下拉刷新; 默认true
					auto: true, // 是否在初始化完毕之后自动执行下拉刷新的回调; 默认true
				},
				// 上拉加载的配置
				upOption: {
					use: false, // 是否启用上拉加载; 默认true
					auto: false
				},
				headerPosition: "relative",
				headerTop: "0px",
				loadingType: 'more', //加载更多状态
				filterIndex: 0,
				Id: 0,
				cateId: 0,
				shopId: 0, //已选三级分类id
				priceOrder: 0, //1 价格从低到高 2价格从高到低
				cateList: [],
				goodsList: [],
				seller:{
					title:'',
					background:''
				}
			};
		},

		// 必须注册滚动到底部的事件,使上拉加载生效
		onReachBottom() {
			this.mescroll && this.mescroll.onReachBottom();
		},
		// 必须注册列表滚动事件,使下拉刷新生效
		onPageScroll(e) {
			this.mescroll && this.mescroll.onPageScroll(e);
		},
		onLoad(options) {
			wx.setNavigationBarTitle({
				title: '店铺'
			})




			this.cateId = options.tid; 
		 
			this.shopId = options.shopid; 
			console.log(this.shopId )
			this.Id = options.sid;
		},

		methods: {

			// mescroll组件初始化的回调,可获取到mescroll对象
			mescrollInit(mescroll) {
				this.mescroll = mescroll;
			},
			// 下拉刷新的回调
			downCallback(mescroll) {
				mescroll.resetUpScroll() // 重置列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			},
			/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
			upCallback(mescroll) {
				let that = this;
				//联网加载数据
				this.getListDataFromNet(common.get_goods_listUrl, mescroll.num, mescroll.size, (curPageData) => {
					//curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

					//联网成功的回调,隐藏下拉刷新和上拉加载的状态;
					//mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
					

					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					//mescroll.endByPage(curPageData.length, totalPage); //必传参数(当前页的数据个数, 总页数)

					//方法二(推荐): 后台接口有返回列表的总数据量 totalSize
					//mescroll.endBySize(curPageData.length, totalSize); //必传参数(当前页的数据个数, 总数据量)

					//方法三(推荐): 您有其他方式知道是否有下一页 hasNext
					//mescroll.endSuccess(curPageData.length, hasNext); //必传参数(当前页的数据个数, 是否有下一页true/false)

					//方法四 (不推荐),会存在一个小问题:比如列表共有20条数据,每页加载10条,共2页.如果只根据当前页的数据个数判断,则需翻到第三页才会知道无更多数据,如果传了hasNext,则翻到第二页即可显示无更多数据.
					mescroll.endSuccess(curPageData.data.length);
console.log(curPageData.seller)
					that.seller=curPageData.seller;
					//设置列表数据
					if (mescroll.num == 1) that.goodsList = []; //如果是第一页需手动制空列表
					that.goodsList = that.goodsList.concat(curPageData.data); //追加新数据
					
					
					
					
				}, () => {
					//联网失败的回调,隐藏下拉刷新的状态
					mescroll.endErr();
				})
			},
			/*联网加载列表数据
			在您的实际项目中,请参考官方写法: http://www.mescroll.com/uni.html#tagUpCallback
			请忽略getListDataFromNet的逻辑,这里仅仅是在本地模拟分页数据,本地演示用
			实际项目以您服务器接口返回的数据为准,无需本地处理分页.
			* */
			getListDataFromNet(url, pageNum, pageSize, successCallback, errorCallback) {
				let that = this;
				console.log(that.filterIndex )
				//延时一秒,模拟联网
				setTimeout(() => {
					try {

						uni.request({
							url: url, //仅为示例，并非真实接口地址。
							data: {
								is_mobile: 1,
								category_id: that.cateId,
								shop_id: that.shopId,
								user_id: 0,
								page_index: pageNum,
								page_size: pageSize,
								keyword: '',
								goods_type: 1,
								order:that.filterIndex,
								priceOrder:that.priceOrder 
							},
							method: 'POST',
							header: {
								'content-type': 'application/x-www-form-urlencoded'
							},
							success: function(res) {
								console.log(res.data.data)
								if (res.data.status == 1) {

									// that.orderList.push(res.data.data)

								} else {



								}
								if (res.data.data == null) {
									res.data.data = [];
								}
								successCallback && successCallback(res.data);





							}
						});

						// 
						// //模拟分页数据
						// var listData = [];
						// for (var i = (pageNum - 1) * pageSize; i < pageNum * pageSize; i++) {
						// 	if (i == mockData.length) break;
						// 	listData.push(mockData[i]);
						// }
						// //联网成功的回调
					} catch (e) {
						//联网失败的回调
						errorCallback && errorCallback();
					}
				}, 10)
			},

			 
			//筛选点击
			tabClick(index) {
				if (this.filterIndex === index && index !== 2) {
					return;
				}
				this.filterIndex = index;
				if (index === 2) {
					this.priceOrder = this.priceOrder === 1 ? 2 : 1;
				} else {
					this.priceOrder = 0;
				} 
				this.mescroll.triggerDownScroll()
				// this.loadData('refresh', 1);
				// uni.showLoading({
				// 	title: '正在加载'
				// })
			},
			//显示分类面板
			toggleCateMask(type) {
				let timer = type === 'show' ? 10 : 300;
				let state = type === 'show' ? 1 : 0;
				this.cateMaskState = 2;
				setTimeout(() => {
					this.cateMaskState = state;
				}, timer)
			},
			//分类点击
			changeCate(item) {
				this.cateId = item.id;
				// this.toggleCateMask();
				// uni.pageScrollTo({
				// 	duration: 300,
				// 	scrollTop: 0
				// })
				// this.loadData('refresh', 1);
				 
			},
			//详情
			navToDetailPage(item) {
				//测试数据没有写id，用title代替
				let id = item.id;
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
			stopPrevent() {}
		},
	}
